import { swap, useAtom } from "@dbeining/react-atom";
import * as React from "react";
import * as State from "./state/state";
import TextField from "@material-ui/core/TextField";
import styled from "styled-components";
import { useState } from "react";

const FlowForm = styled.form`
  display: flex;
  flex-direction: column;
  margin-right: 20%;
`;

const MarginTextField = styled(TextField)`
  && {
    margin-top: 10px;
  }
`;

export const SettingsView = () => {
  const appSettings = useAtom(State.appSettings);

  return (
    <>
      <FlowForm>
        <MarginTextField
          id="interest"
          name="interest"
          label="Kalkylrnta %"
          value={appSettings.interest * 100}
          onChange={handleInterestChange}
        />
        <MarginTextField
          id="period"
          label="Period (mnader)"
          value={appSettings.nrOfMonths}
          onChange={handleNrOfMonthsChange}
        />
        <MarginTextField
          id="vat"
          label="Full moms %"
          value={appSettings.vat * 100}
          onChange={handleVatChange}
        />
        <MarginTextField
          id="socialTaxes"
          label="Sociala avgifter %"
          value={appSettings.socialTaxes * 100}
          onChange={handleSocialTaxesChange}
        />
        <MarginTextField
          id="fuelCompDiesel"
          label="Brnsleersttning fossila brnslen (kr/mil)"
          value={appSettings.fuelCompensationFossil}
          onChange={handleFuelCompensationFossilChange}
        />
        <MarginTextField
          id="fuelCompElectric"
          label="Brnsleersttning el (kr/mil)"
          value={appSettings.fuelCompensationElectric}
          onChange={handleFuelCompensationElectricChange}
        />
        <MarginTextField
          id="compTaxFree"
          label="Bilersttning skattefri del (kr/mil)"
          value={appSettings.carCompensationTaxFree}
          onChange={handleCarCompensationTaxFreeChange}
        />
        <MarginTextField
          id="compTaxed"
          label="Bilersttning skattepliktig del (kr/mil)"
          value={appSettings.carCompensationTaxed}
          onChange={handleCarCompensationTaxedChange}
        />
        <MarginTextField
          id="other"
          label="vriga bilkostnader (kr/mn)"
          value={appSettings.other}
          onChange={handleOtherChange}
        />
        <MarginTextField
          id="promotionDeduction"
          label="Avdrag stripad bil (kr/mn)"
          value={appSettings.promotionDeduction}
          onChange={handlePromotionDeductionChange}
        />
      </FlowForm>
    </>
  );
};

const handleInterestChange = (ev: React.ChangeEvent<HTMLInputElement>) =>
  swap(State.appSettings, (settings) => ({
    ...settings,
    interest: parseFloat(ev.currentTarget.value) / 100,
  }));

const handleNrOfMonthsChange = (ev: React.ChangeEvent<HTMLInputElement>) =>
  swap(State.appSettings, (settings) => ({
    ...settings,
    nrOfMonths: parseInt(ev.currentTarget.value),
  }));

const handleVatChange = (ev: React.ChangeEvent<HTMLInputElement>) =>
  swap(State.appSettings, (settings) => ({
    ...settings,
    vat: parseFloat(ev.currentTarget.value) / 100,
  }));

const handleSocialTaxesChange = (ev: React.ChangeEvent<HTMLInputElement>) =>
  swap(State.appSettings, (settings) => ({
    ...settings,
    socialTaxes: parseFloat(ev.currentTarget.value) / 100,
  }));

const handleFuelCompensationFossilChange = (
  ev: React.ChangeEvent<HTMLInputElement>
) =>
  swap(State.appSettings, (settings) => ({
    ...settings,
    fuelCompensationFossil: parseFloat(ev.currentTarget.value),
  }));

const handleFuelCompensationElectricChange = (
  ev: React.ChangeEvent<HTMLInputElement>
) =>
  swap(State.appSettings, (settings) => ({
    ...settings,
    fuelCompensationElectric: parseFloat(ev.currentTarget.value),
  }));

const handleCarCompensationTaxFreeChange = (
  ev: React.ChangeEvent<HTMLInputElement>
) =>
  swap(State.appSettings, (settings) => ({
    ...settings,
    carCompensationTaxFree: parseFloat(ev.currentTarget.value),
  }));

const handleCarCompensationTaxedChange = (
  ev: React.ChangeEvent<HTMLInputElement>
) =>
  swap(State.appSettings, (settings) => ({
    ...settings,
    carCompensationTaxed: parseFloat(ev.currentTarget.value),
  }));

const handleOtherChange = (ev: React.ChangeEvent<HTMLInputElement>) =>
  swap(State.appSettings, (settings) => ({
    ...settings,
    other: parseInt(ev.currentTarget.value),
  }));

const handlePromotionDeductionChange = (
  ev: React.ChangeEvent<HTMLInputElement>
) =>
  swap(State.appSettings, (settings) => ({
    ...settings,
    promotionDeduction: parseInt(ev.currentTarget.value),
  }));
