import { Fuel, Settings } from "./state/state";

export function annuitet(amount: number, settings: Settings): number {
  const mi = settings.interest / 12;
  return (
    (amount * (mi * Math.pow(1 + mi, settings.nrOfMonths))) /
    (Math.pow(1 + mi, settings.nrOfMonths) - 1)
  );
}
export function withRest(
  price: number,
  downpayment: number,
  rest: number,
  settings: Settings
): number {
  const monthly = (price * rest * settings.interest) / 12;
  return monthly + annuitet(price * (1 - rest) - downpayment, settings);
}

export function milageAllowance(miles: number, fuel: Fuel): number {
  if (fuel === Fuel.Fossil) {
    return 6.5 * miles;
  } else {
    return 9 * miles;
  }
}
export function totalCost(
  price: number,
  downpayment: number,
  rest: number,
  benefitValue: number,
  fuel: Fuel,
  workMiles: number,
  settings: Settings
): number {
  const leaseExVat =
    withRest(price, downpayment, rest, settings) / (1 + settings.vat / 2);
  const socialTaxes = benefitValue * settings.socialTaxes;
  return (
    leaseExVat +
    milageAllowance(workMiles, fuel) +
    settings.other +
    socialTaxes -
    settings.promotionDeduction
  );
}
export function salaryDeduction(
  price: number,
  downpayment: number,
  rest: number,
  benefitValue: number,
  fuel: Fuel,
  workMiles: number,
  settings: Settings
): number {
  const milageIncome =
    workMiles *
    (settings.carCompensationTaxFree +
      settings.carCompensationTaxed * (1 + settings.socialTaxes));
  return (
    (totalCost(
      price,
      downpayment,
      rest,
      benefitValue,
      fuel,
      workMiles,
      settings
    ) -
      milageIncome) /
    (1 + settings.socialTaxes)
  );
}

export function netDeduction(
  price: number,
  downpayment: number,
  rest: number,
  benefitValue: number,
  fuel: Fuel,
  workMiles: number,
  marginalTax: number,
  settings: Settings
): number {
  return (
    salaryDeduction(
      price,
      downpayment,
      rest,
      benefitValue,
      fuel,
      workMiles,
      settings
    ) *
    (1 - marginalTax)
  );
}

export function netBenefitCost(
  benefitValue: number,
  marginalTax: number
): number {
  return benefitValue * marginalTax;
}

export function workMilesCompensation(
  fuel: Fuel,
  workMiles: number,
  settings: Settings
): number {
  return (
    workMiles *
    (fuel === Fuel.Fossil
      ? settings.fuelCompensationFossil
      : settings.fuelCompensationElectric)
  );
}

export function netCosts(
  price: number,
  downpayment: number,
  rest: number,
  benefitValue: number,
  fuel: Fuel,
  workMiles: number,
  marginalTax: number,
  settings: Settings
): number {
  return (
    netDeduction(
      price,
      downpayment,
      rest,
      benefitValue,
      fuel,
      workMiles,
      marginalTax,
      settings
    ) + netBenefitCost(benefitValue, marginalTax)
  );
}
