import * as React from "react";
import {
  salaryDeduction,
  netDeduction,
  workMilesCompensation,
  netCosts,
  netBenefitCost,
} from "./carCalcLogic";
import { Checkbox, FormControlLabel, Typography } from "@material-ui/core";
import { Slider } from "@material-ui/core";
import styled from "styled-components";
import * as State from "./state/state";
import { useAtom, swap } from "@dbeining/react-atom";
import { Fuel } from "./state/state";

const FlexForm = styled.form`
  display: flex;
  flex-direction: column;
`;

const PaddedSlider = styled(Slider)`
  padding: 11px 0px;
`;

export const CarCalcView = () => {
  const appState = useAtom(State.appState);
  const appSettings = useAtom(State.appSettings);

  return (
    <FlexForm>
      <Typography variant="caption">Totalt pris:</Typography>
      <Typography variant="body1">{appState.price}</Typography>
      <PaddedSlider
        min={200000}
        max={1200000}
        step={5000}
        onChange={handlePriceChange}
        value={appState.price}
      />
      <Typography variant="caption">Frsta amortering:</Typography>
      <Typography variant="body1">{appState.initialDownPayment}</Typography>
      <PaddedSlider
        min={0}
        max={300000}
        step={5000}
        onChange={handleinitialDownPaymentChange}
        value={appState.initialDownPayment}
      />
      <Typography variant="caption">Frmnsvrde:</Typography>
      <Typography variant="body1">{appState.benefit}</Typography>
      <PaddedSlider
        min={1500}
        max={15000}
        step={50}
        value={appState.benefit}
        onChange={handleBenefitChange}
      />
      <Typography variant="caption">Restvrde:</Typography>
      <Typography variant="body1">{appState.rest * 100}%</Typography>
      <PaddedSlider
        min={20}
        max={45}
        step={5}
        value={appState.rest * 100}
        onChange={handleRestChange}
      />
      <FormControlLabel
        control={
          <Checkbox
            checked={appState.fuel === State.Fuel.Fossil}
            onChange={handleFuelChange}
            name="fossilCheckbox"
            color="primary"
          />
        }
        label="Fossilt brnsle"
      />
      <Typography variant="caption">Tjnstemil (per mnad):</Typography>
      <Typography variant="body1">{appState.businessMiles}</Typography>
      <PaddedSlider
        min={0}
        max={400}
        step={5}
        value={appState.businessMiles}
        onChange={handleBusinessMilesChange}
      />
      <Typography variant="caption">Lneavdrag brutto:</Typography>
      <Typography variant="body2">
        {salaryDeduction(
          appState.price,
          appState.initialDownPayment,
          appState.rest,
          appState.benefit,
          State.Fuel.Electric,
          appState.businessMiles,
          appSettings
        ).toFixed(0)}
      </Typography>
      <Typography variant="caption">Marginalskatt:</Typography>
      <Typography variant="body1">
        {(appState.marginalTax * 100).toFixed(0)}%
      </Typography>
      <PaddedSlider
        min={0.3}
        max={0.65}
        step={0.01}
        value={appState.marginalTax}
        onChange={handleMarginalTaxChange}
      />
      <Typography variant="caption">Lneavdrag netto:</Typography>
      <Typography variant="body2">
        {netDeduction(
          appState.price,
          appState.initialDownPayment,
          appState.rest,
          appState.benefit,
          State.Fuel.Electric,
          appState.businessMiles,
          appState.marginalTax,
          appSettings
        ).toFixed(0)}
      </Typography>
      <Typography variant="caption">Nettokostnad frmnsvrde:</Typography>
      <Typography variant="body2">
        {netBenefitCost(appState.benefit, appState.marginalTax).toFixed(0)}
      </Typography>
      <Typography variant="body1">Total nettokostnad:</Typography>
      <Typography variant="body1">
        {netCosts(
          appState.price,
          appState.initialDownPayment,
          appState.rest,
          appState.benefit,
          State.Fuel.Electric,
          appState.businessMiles,
          appState.marginalTax,
          appSettings
        ).toFixed(0)}
      </Typography>
      <Typography variant="caption">
        Brnsleersttning (skattefritt):
      </Typography>
      <Typography variant="body2">
        {workMilesCompensation(
          appState.fuel,
          appState.businessMiles,
          appSettings
        ).toFixed(0)}
      </Typography>
    </FlexForm>
  );
};

const handlePriceChange = (_: unknown, value: number | number[]) =>
  swap(State.appState, (s) => ({ ...s, price: value as number }));

const handleinitialDownPaymentChange = (_: unknown, value: number | number[]) =>
  swap(State.appState, (s) => ({ ...s, initialDownPayment: value as number }));

const handleMarginalTaxChange = (_: unknown, value: number | number[]) =>
  swap(State.appState, (s) => ({ ...s, marginalTax: value as number }));

const handleRestChange = (_: unknown, value: number | number[]) =>
  swap(State.appState, (s) => ({
    ...s,
    rest: (value as number) / 100,
  }));

const handleBenefitChange = (_: unknown, value: number | number[]) =>
  swap(State.appState, (s) => ({
    ...s,
    benefit: value as number,
  }));

const handleFuelChange = (event: React.ChangeEvent<HTMLInputElement>) =>
  swap(State.appState, (s) => ({
    ...s,
    fuel: event.target.checked ? Fuel.Fossil : Fuel.Electric,
  }));

const handleBusinessMilesChange = (_: unknown, value: number | number[]) =>
  swap(State.appState, (s) => ({
    ...s,
    businessMiles: value as number,
  }));
